import React, { useEffect, useRef } from 'react';
import Victor from 'victor';
import './index.css';
import enemy from './images/enemy.png';
import food from './images/food.png';
import turtle from './images/turtle.png';
import turtleTransparent from './images/turtleTransparent.png';
import bubble from './images/bubble.png';
import shark from './images/shark.png';
import freeze from './images/freeze.png';

const CANVAS_X = 1200;
const CANVAS_Y = 800;

const DRAW_INTERVAL = 1;
const GAME_LOOP_INTERVAL = 1;

const ADD_JELLY_INTERVAL = 250;
const BASE_JELLY_SPEED = 0.2;
const SPEED_UP_INTERVAL = 1000;
const SPEED_UP = 0.015;
const LEVEL_FADE = 600;
const ADD_SHARK_INTERVAL = 5000;

const PLAYER_SPEED = 1;
const LIVES = 5;
const LIVES_FADE = 600;
const MIN_DASH = -5;
const MAX_DASH = 200;
const DASH = 3;
const SLOWDOWN = 2000;
const CHARGE_DASH_INTERVAL = 10;

type Jelly = {
  x: number;
  y: number;
  dx: number;
  dy: number;
  a: boolean;
  o: number;
}

type Bubble = {
  x: number;
  y: number;
  s: number;
  o: number;
}

type Shark = {
  x: number;
  y: number;
  o: number;
  ltr: boolean;
}

const getScaleFactor = (index: number, offset: number, baseScale: number, pulseScale: number) => {
  const mod = ((index + offset) % 250) / 2.5;
  return baseScale * (1 + (mod < 65 ? mod / 65 : (100 - mod) / 35) * pulseScale);
}

const Game = () => {

  const instructions = useRef(true);
  const gameLoopCounter = useRef<number>(0);
  const level = useRef<number>(1);
  const levelFade = useRef<number>(LEVEL_FADE);
  const lives = useRef<number>(LIVES);
  const livesFade = useRef<number>(0);
  const x = useRef<number>(0);
  const y = useRef<number>(0);
  const angle = useRef<number>(Math.PI);
  const freezes = useRef<number>(5);
  const dashRef = useRef<number>(MAX_DASH);
  const canvasRef = useRef<HTMLCanvasElement|null>(null);
  const canvasCtxRef = useRef<CanvasRenderingContext2D|null>(null);
  const keysDown = useRef<Set<string>>(new Set());
  const jellies = useRef<Jelly[]>([]);
  const jelliesAdded = useRef<number>(0);
  const jelliesEaten = useRef<number>(0);
  const slowDown = useRef<number>(0);
  const jellySpeed = useRef<number>(BASE_JELLY_SPEED);
  const enemyRef = useRef<HTMLImageElement|null>(null);
  const foodRef = useRef<HTMLImageElement|null>(null);
  const turtleRef = useRef<HTMLImageElement|null>(null);
  const turtleTransparentRef = useRef<HTMLImageElement|null>(null);
  const bubbleRef = useRef<HTMLImageElement|null>(null);
  const bubbles = useRef<Bubble[]>([]);
  const sharkRef = useRef<HTMLImageElement|null>(null);
  const sharks = useRef<Shark[]>([]);
  const freezeRef = useRef<HTMLImageElement|null>(null);

  useEffect(() => {
    if (canvasRef.current) {
      canvasRef.current.focus();
      canvasRef.current.width = CANVAS_X;
      canvasRef.current.height = CANVAS_Y;
      canvasCtxRef.current = canvasRef.current.getContext('2d');
      x.current = canvasRef.current.width / 2;
      y.current = canvasRef.current.height / 2;
    }

    const drawInterval = setInterval(() => {
      draw();
    }, DRAW_INTERVAL);

    const gameLoopInterval = setInterval(() => {
      gameLoop();
    }, GAME_LOOP_INTERVAL);

    return () => {
      clearInterval(drawInterval);
      clearInterval(gameLoopInterval);
    }
  }, []);

  const gameLoop = () => {
    gameLoopCounter.current = (gameLoopCounter.current + 1) % (Number.MAX_SAFE_INTEGER - 1);
    if (gameLoopCounter.current % ADD_JELLY_INTERVAL === 0) addBubble();
    if (!instructions.current) {
      if (gameLoopCounter.current % ADD_JELLY_INTERVAL === 0) addJelly();
      if (gameLoopCounter.current % ADD_SHARK_INTERVAL === 0) addShark();
      if (gameLoopCounter.current % SPEED_UP_INTERVAL === 0) speedUp();
      if (gameLoopCounter.current % CHARGE_DASH_INTERVAL === 0) chargeDash();
    }
    playerInput();
    updatePositions();
    collisionDetection();
    slowDown.current = slowDown.current > 0 ? slowDown.current - 1 : 0;
    levelFade.current = levelFade.current > 0 ? levelFade.current - 1 : 0;
  }

  const addJelly = () => {
    if (jelliesAdded.current === level.current) return;
    const v = new Victor(1, 0).multiplyScalar(jellySpeed.current).rotateDeg(Math.random() * 360);
    jellies.current.push({
      x: Math.random() > 0.5 ? canvasRef.current?.width! : 0,
      y: Math.random() > 0.5 ? canvasRef.current?.height! : 0,
      dx: v.x,
      dy: v.y,
      a: jellies.current.length + 1 === level.current,
      o: Math.floor(Math.random() * 100)
    });
    jelliesAdded.current++;
  }

  const addBubble = () => {
    bubbles.current.push({
      x: Math.random() * canvasRef.current?.width!,
      y: canvasRef.current?.height!,
      s: Math.random() * 0.9 + 0.1,
      o: Math.floor(Math.random() * 100)
    });
  }

  const addShark = () => {
    console.log('lives', lives.current);
    if (lives.current <= 0) return;
    const ltr = Math.random() < 0.5;
    sharks.current.push({
      x: ltr ? -50 : canvasRef.current?.width! + 50,
      y: Math.floor(Math.random() * canvasRef.current?.height! * 0.5) + 50,
      o: Math.floor(Math.random() * 100),
      ltr
    });
  }

  const speedUp = () => {
    jellySpeed.current += SPEED_UP;
    for (const jelly of jellies.current) {
      const v = new Victor(jelly.dx, jelly.dy);
      const v2 = v.normalize().multiplyScalar(jellySpeed.current);
      jelly.dx = v2.x;
      jelly.dy = v2.y;
    }
  }

  const chargeDash = () => {
    dashRef.current += (MAX_DASH / 400);
    if (dashRef.current > MAX_DASH) dashRef.current = MAX_DASH;
  }

  const playerInput = () => {
    if (lives.current > 0) {
      const dashing = keysDown.current.has(' ') && dashRef.current > 0;
      let increment = dashing ? PLAYER_SPEED * DASH : PLAYER_SPEED;
      let dx = 0;
      let dy = 0;
      if (keysDown.current.has('w') || keysDown.current.has('ArrowUp')) dy -= increment;
      if (keysDown.current.has('a') || keysDown.current.has('ArrowLeft')) dx -= increment;
      if (keysDown.current.has('s') || keysDown.current.has('ArrowDown')) dy += increment;
      if (keysDown.current.has('d') || keysDown.current.has('ArrowRight')) dx += increment;
      if (dx !== 0 || dy !== 0) {
        if (dashing && dashRef.current > MIN_DASH && levelFade.current <= 0) dashRef.current--;
        x.current += dx;
        y.current += dy;
        x.current = x.current < 0 ? 0 : (x.current > canvasRef.current?.width! ? canvasRef.current?.width! : x.current);
        y.current = y.current < 0 ? 0 : (y.current > canvasRef.current?.height! ? canvasRef.current?.height! : y.current);
        angle.current = Math.atan2(dy, dx) - Math.PI / 2;
      }
    }
  }

  const updatePositions = () => {
    for (const jelly of jellies.current) {
      jelly.x += ((SLOWDOWN - slowDown.current) / SLOWDOWN) * jelly.dx;
      jelly.y += ((SLOWDOWN - slowDown.current) / SLOWDOWN) * jelly.dy;
    }
    for (const bubble of bubbles.current) {
      bubble.y -= 0.4;
    }
    for (const shark of sharks.current) {
      shark.x += ((SLOWDOWN - slowDown.current) / SLOWDOWN) * jellySpeed.current * (shark.ltr ? 1 : -1);
    }
  }

  const collisionDetection = () => {
    let playerCollided = false;
    for (const jelly of jellies.current) {
      if (jelly.x > canvasRef.current?.width! || jelly.x < 0) {
        jelly.dx = 0 - jelly.dx;
      }
      if (jelly.y > canvasRef.current?.height! || jelly.y < 0) {
        jelly.dy = 0 - jelly.dy;
      }
    }
    for (let i = 0; i < jellies.current.length; i++) {
      const jelly = jellies.current[i];
      const v = new Victor(jelly.x - x.current, jelly.y - y.current);
      if (v.magnitude() <= 35) {
        if (jelly.a) {
          jellies.current.splice(i, 1);
          jelliesEaten.current++;
          if (jellies.current.length > 0) {
            jellies.current[Math.floor(Math.random() * (jellies.current.length - 1))].a = true;
            dashRef.current = Math.min(dashRef.current + 0.5 * MAX_DASH, MAX_DASH);
          } else {
            levelComplete();
          }
        } else {
          playerCollided = true;
        }
      }
    }
    for (const shark of sharks.current) {
      if (Math.abs(shark.y - y.current) < 40 && Math.abs(shark.x - x.current) < 90) {
        playerCollided = true;
      }
    }
    for (let i = 0; i < bubbles.current.length; i++) {
      if (bubbles.current[i].y < -15) {
        bubbles.current.splice(i, 1);
      }
    }
    for (let i = 0; i < sharks.current.length; i++) {
      if (sharks.current[i].x < -50 || sharks.current[i].x > canvasRef.current?.width! + 50) {
        sharks.current.splice(i, 1);
      }
    }
    if (playerCollided && livesFade.current <= 0) loseALife();
    /*
    for (let i = 0; i < jellies.current.length; i++) {
      for (let j = i + 1; j < jellies.current.length; j++) {
        const jelly1 = jellies.current[i];
        const jelly2 = jellies.current[j];
        if (Math.sqrt(Math.pow(jelly1.x - jelly2.x, 2) + Math.pow(jelly1.y - jelly2.y, 2)) <= 30) {
          if (jelly1.dx === 0 && jelly1.dy === 0 && jelly2.dx === 0 && jelly2.dy === 0) continue;
          const v1 = new Victor(jelly1.dx, jelly1.dy);
          const n1 = new Victor(jelly2.x - jelly1.x, jelly2.y - jelly1.y).normalize();
          const r1 = v1.subtract(n1.multiplyScalar(2 * v1.dot(n1)));
          console.log('changing dx', jelly1.dx, 'to', r1.x);
          console.log('changing dy', jelly1.dy, 'to', r1.y);
          jelly1.dx = r1.x;
          jelly1.dy = r1.y;

          const v2 = new Victor(jelly2.dx, jelly2.dy);
          const n2 = new Victor(jelly1.x - jelly2.x, jelly1.y - jelly2.y).normalize();
          const r2 = v2.subtract(n2.multiplyScalar(2 * v2.dot(n2)));
          console.log('changing dx', jelly2.dx, 'to', r2.x);
          console.log('changing dy', jelly2.dy, 'to', r2.y);
          jelly2.dx = r2.x;
          jelly2.dy = r2.y;
          
          console.log('--');

        }
      }
    }
    */
  }

  const newGame = () => {
    lives.current = LIVES;
    livesFade.current = 0;
    x.current = canvasRef.current!.width / 2;
    y.current = canvasRef.current!.height / 2;
    angle.current = Math.PI;
    level.current = 1;
    levelFade.current = LEVEL_FADE;
    jellies.current = [];
    jellySpeed.current = BASE_JELLY_SPEED;
    jelliesAdded.current = 0;
    jelliesEaten.current = 0;
    freezes.current = 5;
    dashRef.current = MAX_DASH;
    slowDown.current = 0;
    sharks.current = [];
    gameLoopCounter.current = 0;
  }

  const levelComplete = () => {
    level.current++;
    jellySpeed.current = BASE_JELLY_SPEED + (level.current / 5) * BASE_JELLY_SPEED;
    jelliesAdded.current = 0;
    levelFade.current = LEVEL_FADE;
    dashRef.current = MAX_DASH;
  }

  const loseALife = () => {
    lives.current--;
    livesFade.current = LIVES_FADE;
    if (lives.current <= 0) {
      jellies.current = [];
      sharks.current = [];
    }
  }

  const draw = () => {
    const ctx = canvasCtxRef.current;
    ctx?.clearRect(0, 0, canvasRef.current?.width!, canvasRef.current?.height!);

    const gradient = ctx!.createLinearGradient(0, canvasRef.current!.height, 0, 0);
    gradient.addColorStop(0, 'rgb(0, 100, 150)');
    gradient.addColorStop(1, 'rgb(200, 255, 255)');
    ctx!.fillStyle = gradient;
    ctx!.fillRect(0, 0, canvasRef.current?.width!, canvasRef.current?.height!);

    if (instructions.current) {
      const scaleFactor = getScaleFactor(gameLoopCounter.current, 0, 1, 0.01);
      ctx!.save();
      ctx!.translate(600, 190);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.font = '100px Luckiest Guy';
      ctx!.textAlign = 'center';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText(`Top Turtle`, 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(320, 280);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.font = '40px Luckiest Guy';
      ctx!.textAlign = 'center';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText('Eat:', 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(320, 360);
      ctx!.scale(scaleFactor * 1.5, scaleFactor * 1.5);
      ctx!.drawImage(foodRef.current!, -15, -15);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(830, 280);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.font = '40px Luckiest Guy';
      ctx!.textAlign = 'center';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText('Avoid:', 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(720, 360);
      ctx!.scale(scaleFactor * 1.5, scaleFactor * 1.5);
      ctx!.drawImage(enemyRef.current!, -15, -15);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(920, 360);
      ctx!.scale(scaleFactor * 3, scaleFactor * 3);
      ctx!.drawImage(sharkRef.current!, -35, -20);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(400, 460);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.textAlign = 'center';
      ctx!.font = '30px Luckiest Guy';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText('W', 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(400, 490);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.textAlign = 'center';
      ctx!.font = '30px Luckiest Guy';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText('A S D', 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(513, 475);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.textAlign = 'center';
      ctx!.font = '30px Luckiest Guy';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText('or', 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(620, 460);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.textAlign = 'center';
      ctx!.font = '30px Luckiest Guy';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText('\u2191', 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(620, 490);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.textAlign = 'center';
      ctx!.font = '30px Luckiest Guy';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText('\u2190 \u2193 \u2192', 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(770, 475);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.textAlign = 'center';
      ctx!.font = '30px Luckiest Guy';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText('to move', 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(350, 590);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.textAlign = 'center';
      ctx!.font = '30px Luckiest Guy';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText(`Hold <Space> to dash`, 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(840, 590);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.textAlign = 'center';
      ctx!.font = '30px Luckiest Guy';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText(`Press <F> to freeze`, 0, 0);
      ctx!.restore();
      ctx!.save();
      ctx!.translate(CANVAS_X / 2, CANVAS_Y - 100);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.textAlign = 'center';
      ctx!.font = '50px Luckiest Guy';
      ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
      ctx!.fillText(`Press <Enter> to play`, 0, 0);
      ctx!.restore();
    } else {
      ctx!.save();
      ctx!.translate(x.current, y.current);
      ctx!.scale(1.4, 1.4);
      ctx!.rotate(angle.current);
      if (livesFade.current > 0) {
        const scaleFactor = getScaleFactor(livesFade.current, 0, 1, 0.2);
        ctx!.scale(scaleFactor, scaleFactor);
        ctx!.drawImage(turtleTransparentRef.current!, -15, -20);
        livesFade.current--;
      } else {
        ctx!.drawImage(turtleRef.current!, -15, -20);
      }
      ctx!.restore();

      for (const jelly of jellies.current) {
        ctx!.save();
        ctx!.translate(jelly.x, jelly.y);
        ctx!.rotate(Math.atan2(jelly.dy, jelly.dx) + Math.PI / 2);
        const scaleFactor = getScaleFactor(gameLoopCounter.current, jelly.o, 1, 0.2);
        ctx!.scale(scaleFactor, scaleFactor);
        if (jelly.a) {
          ctx!.drawImage(foodRef.current!, -15, -15);
        } else {
          ctx!.drawImage(enemyRef.current!, -15, -15);
        }
        ctx!.restore();
      }

      for (const shark of sharks.current) {
        ctx!.save();
        ctx!.translate(shark.x, shark.y);
        if (shark.ltr) ctx!.scale(-1, 1);
        const scaleFactor = getScaleFactor(gameLoopCounter.current, shark.o, 2, 0.05);
        ctx!.scale(scaleFactor, scaleFactor);
        ctx!.drawImage(sharkRef.current!, -35, -20);
        ctx!.restore();
      }
    }

    for (const bubble of bubbles.current) {
      ctx!.save();
      ctx!.translate(bubble.x, bubble.y);
      const scaleFactor = getScaleFactor(gameLoopCounter.current, bubble.o, bubble.s, 0.2);
      ctx!.scale(scaleFactor, scaleFactor);
      ctx!.drawImage(bubbleRef.current!, -15, -15);
      ctx!.restore();
    }

    if (!instructions.current) {
      for (let i = 0; i < freezes.current; i++) {
        ctx!.save();
        ctx!.translate(canvasRef.current?.width! - 37 * i - 22, 20);
        ctx!.drawImage(freezeRef.current!, -15, -15);
        ctx!.restore();
      }

      ctx!.beginPath();
      ctx!.rect(10, 10, 200, 20);
      ctx!.lineWidth = 3;
      ctx!.strokeStyle = 'rgb(0, 0, 0, 20%)';
      ctx!.stroke();
      ctx!.closePath();
      ctx!.beginPath();
      ctx!.rect(10, 10, Math.max(0, (dashRef.current / MAX_DASH)) * 200, 20);
      ctx!.fillStyle = 'rgb(0, 255, 0, 20%)';
      ctx!.fill();
      ctx!.closePath();

      for (let i = 0; i < lives.current; i++) {
        ctx!.save();
        ctx!.translate(35 + 45 * i, canvasRef.current?.height! - 30);
        ctx!.rotate(Math.PI);
        ctx!.drawImage(turtleTransparentRef.current!, -15, -15);
        ctx!.restore();
      }

      if (levelFade.current > 0) {
        ctx!.save();
        ctx!.translate(canvasRef.current?.width! / 2, canvasRef.current?.height! / 2);
        const scaleFactor = 0.6 * levelFade.current / LEVEL_FADE + 0.25;
        ctx!.scale(scaleFactor, scaleFactor);
        ctx!.font = '150px Luckiest Guy';
        ctx!.textAlign = 'center';
        ctx!.textBaseline = 'middle';
        ctx!.fillStyle = `rgba(0, 0, 0, ${ (levelFade.current * 0.4) / LEVEL_FADE })`;
        ctx!.fillText(`LEVEL ${level.current}`, 0, 0);
        ctx!.restore();
      }

      if (lives.current <= 0) {
        const scaleFactor = getScaleFactor(gameLoopCounter.current, 0, 1, 0.01);
        ctx!.save();
        ctx!.translate(canvasRef.current?.width! / 2, canvasRef.current?.height! / 2 - 60);
        ctx!.scale(scaleFactor, scaleFactor);
        ctx!.font = '100px Luckiest Guy';
        ctx!.textAlign = 'center';
        ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
        ctx!.fillText(`GAME OVER`, 0, 0);
        ctx!.restore();
        ctx!.save();
        ctx!.translate(canvasRef.current?.width! / 2, canvasRef.current?.height! / 2 + 40);
        ctx!.scale(scaleFactor, scaleFactor);
        ctx!.font = '60px Luckiest Guy';
        ctx!.textAlign = 'center';
        ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
        ctx!.fillText(`Score: ${ jelliesEaten.current }`, 0, 0);
        ctx!.restore();
        ctx!.save();
        ctx!.translate(canvasRef.current?.width! / 2, canvasRef.current?.height! / 2 + 180);
        ctx!.scale(scaleFactor, scaleFactor);
        ctx!.textAlign = 'center';
        ctx!.font = '40px Luckiest Guy';
        ctx!.fillStyle = `rgb(0, 0, 0, 50%)`;
        ctx!.fillText(`Press <Enter> to play again`, 0, 0);
        ctx!.restore();
      } else {
        ctx!.save();
        ctx!.translate(canvasRef.current?.width! - 15, canvasRef.current?.height! - 17);
        ctx!.font = '30px Luckiest Guy';
        ctx!.textAlign = 'right';
        ctx!.fillStyle = `rgb(0, 0, 0, 20%)`;
        ctx!.fillText(`Score: ${ jelliesEaten.current }`, 0, 0);
        ctx!.restore();
      }
    }
  }

  const onKeyDown: React.KeyboardEventHandler<HTMLCanvasElement> = e => {
    if (instructions.current) {
      if (e.key === 'Enter') {
        instructions.current = false;
        gameLoopCounter.current = 0;
      }
    } else {
      if (e.key === 'f' && !keysDown.current.has('f') && freezes.current > 0) {
        freezes.current--;
        slowDown.current = SLOWDOWN;
      }
      if (e.key === 'Enter' && lives.current === 0) {
        newGame();
      }
    }
    keysDown.current.add(e.key)
  }

  return (
    <>
      <img ref={enemyRef} src={enemy} className="hidden" alt="enemy" />
      <img ref={foodRef} src={food} className="hidden" alt="food" />
      <img ref={turtleRef} src={turtle} className="hidden" alt="turtle" />
      <img ref={turtleTransparentRef} src={turtleTransparent} className="hidden" alt="turtle" />
      <img ref={bubbleRef} src={bubble} className="hidden" alt="bubble" />
      <img ref={sharkRef} src={shark} className="hidden" alt="shark" />
      <img ref={freezeRef} src={freeze} className="hidden" alt="freeze" />
      <canvas
        tabIndex={0}
        ref={canvasRef}
        onKeyDown={onKeyDown}
        onKeyUp={e => void keysDown.current.delete(e.key)}
        onBlur={e => {
          e.preventDefault();
          const target = e.currentTarget;
          setTimeout(() => { target.focus(); }, 0);
        }}
      />
    </>
  )
}

export default Game;